@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables";

.main {
  display: block;
  flex-grow: 1;

  &__app {
    padding: 10px 0;
    border-bottom: 1px solid #e5e5e5;
  }
}

.email {
  display: block;
  font-weight: 600;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
  border-bottom: 1px solid #e5e5e5;
  margin-top: 20px;

  &__title {
    color: var(--text-primary);
    font-weight: 600;
    font-size: 16px;
  }

  &__storage {
    background: var(--lighter-primary);
    border-radius: 2px;
    padding: 3px;
  }
}

.row {
  display: flex;

  @include notebook {
    flex-direction: column-reverse;
  }
}

.upload {
  flex: 0 0 200px;
  margin-left: 40px;

  @include notebook {
    margin: 0 auto 20px;
  }

  &__input {
    opacity: 0;
    position: absolute;
    z-index: -1;
  }

  &__box {
    background: var(--bg);
    border: 1px solid #ebebeb;
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.08);
    border-radius: 8px;
    padding: 15px;
  }

  &__button {
    display: block;
    background: $primary;
    border-radius: 2px;
    color: #fff;
    font-size: 14px;
    padding: 7px;
    width: 100%;

    & + .upload__button {
      margin-top: 10px;
    }
  }

  &__num {
    display: block;
    text-align: center;
    font-size: 12px;
    margin: 10px 0;
  }

  &__item {
    border-top: 1px solid #e5e5e5;
    padding: 10px 0;

    &:last-child {
      padding-bottom: 0;
    }
  }

  &__title {
    font-size: 12px;
    font-weight: 500;
    word-break: break-word;
  }

  &__progress-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: $text-secondary;
    font-size: 12px;
    margin-top: 4px;
  }

  &__progress {
    flex: 0 0 130px;
    height: 3px;
    position: relative;
    background: #d8d8d8;
    border-radius: 2px;

    &-inner {
      height: 3px;
      position: absolute;
      background: $primary;
      border-radius: 2px;
      width: 0;
    }
  }

  &__status {
    display: block;
    word-break: break-word;
    background: var(--bg-success);
    border-radius: 2px;
    color: #3c4f3e;
    font-size: 12px;
    padding: 3px;
    margin: 6px 0;

    &_error {
      background: var(--bg-error);
      color: var(--configure-error-text);
    }
  }

  &__date {
    display: flex;
    justify-content: space-between;
    color: $text-secondary;
    font-size: 10px;
  }

  &__delete {
    margin-left: 5px;
    cursor: pointer;
  }
}

.help {
  font-size: 12px;
  margin: 10px 10px 0;
  padding-top: 10px;
  border-top: 1px solid #e5e5e5;
  display: flex;
  align-items: flex-start;

  &__icon {
    margin-top: 3px;
    margin-right: 5px;
  }
}
